export default ()=> {
      return (
        <div className="question" data-index="150" dangerouslySetInnerHTML = {{ __html: `
<h6>150. 输出什么?</h6>
<pre class="language-javascript"><code class="language-javascript"><span class="token keyword keyword-const">const</span> animals <span class="token operator">=</span> <span class="token punctuation">{</span><span class="token punctuation">}</span><span class="token punctuation">;</span>
<span class="token keyword keyword-let">let</span> dog <span class="token operator">=</span> <span class="token punctuation">{</span> emoji<span class="token operator">:</span> <span class="token string">'🐶'</span> <span class="token punctuation">}</span>
<span class="token keyword keyword-let">let</span> cat <span class="token operator">=</span> <span class="token punctuation">{</span> emoji<span class="token operator">:</span> <span class="token string">'🐈'</span> <span class="token punctuation">}</span>

animals<span class="token punctuation">[</span>dog<span class="token punctuation">]</span> <span class="token operator">=</span> <span class="token punctuation">{</span> <span class="token operator">...</span>dog<span class="token punctuation">,</span> name<span class="token operator">:</span> <span class="token string">"Mara"</span> <span class="token punctuation">}</span>
animals<span class="token punctuation">[</span>cat<span class="token punctuation">]</span> <span class="token operator">=</span> <span class="token punctuation">{</span> <span class="token operator">...</span>cat<span class="token punctuation">,</span> name<span class="token operator">:</span> <span class="token string">"Sara"</span> <span class="token punctuation">}</span>

console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span>animals<span class="token punctuation">[</span>dog<span class="token punctuation">]</span><span class="token punctuation">)</span>
</code></pre>
<ul>
<li>A: <code>{ emoji: &quot;🐶&quot;, name: &quot;Mara&quot; }</code></li>
<li>B: <code>{ emoji: &quot;🐈&quot;, name: &quot;Sara&quot; }</code></li>
<li>C: <code>undefined</code></li>
<li>D: <code>ReferenceError</code></li>
</ul>
<details><summary><b>答案</b></summary>
<p>
<h4>答案: B</h4>
<p>对象的键会被转换为字符串。</p>
<p>因为  <code>dog</code> 的值是一个对象，   <code>animals[dog]</code> 实际上意味着我们创建了一个叫做 <code>&quot;object Object&quot;</code> 的属性来代表新的对象。  <code>animals[&quot;object Object&quot;]</code> 现在等于 <code>{ emoji: &quot;🐶&quot;, name: &quot;Mara&quot;}</code>。</p>
<p><code>cat</code> 也是一个对象，<code>animals[cat]</code> 实际上意味着我们在用新的cat的属性覆盖  <code>animals[\`\`&quot;\`\`object Object\`\`&quot;\`\`]</code> 的值。</p>
<p>打印  <code>animals[dog]</code>，实际上是<code>animals[&quot;object Object&quot;]</code>，这是因为转化<code>dog</code>对象为一个字符串结果 <code>&quot;object Object&quot;</code> ，所以返回 <code>{ emoji: &quot;🐈&quot;, name: &quot;Sara&quot; }</code>。</p>
</p>
</details>
` }}></div>
      );
    }